import { Component } from "react"
import styled from "styled-components"
import { ApiGeneric } from "src/services/apiServices"
import { HFormat, HUtils } from "@macashipo/mlib"
import { MyModal } from "src/core/components"
import { Animated } from "react-animated-css"
import classNames from "classnames"
class MUIPJAPI extends Component {
  constructor(props) {
    super(props)
    this.state = {
      loading: false,
      data: null,
    }
  }
  componentDidMount() {
    this.requestList()
    SharedSET.reloadBtn = () => {
      if (SharedSET.data) {
        this.setState({
          data: SharedSET.data,
        })
      }
    }
  }

  requestList = () => {
    this.setState(
      {
        loading: true,
      },
      () => {
        ApiGeneric({
          request: {
            method: "POST",
            path: "/api/v1/TimeTrackingSystem_Mine",
            name: "List",
          },
          data: {},
          successCallBack: response => {
            console.log("res:", response)
            let _data = []
            if (response.Data && response.Data.Data) {
              _data = response.Data.Data
            }
            this.setState(
              {
                data: _data,
                loading: false,
              },
              () => {
                SharedSET.data = _data
              }
            )
          },
          errorCallBack: err => {
            console.log(err)
            this.setState({
              loading: false,
            })
          },
        })
      }
    )
  }

  render() {
    const data = this.state.data
    const showWarning = data != null && data.length == 0
    if (this.state.loading) {
      return (
        <button
          disabled
          className={classNames("btn btn-primary")}
          onClick={() => {}}
        >
          <i className="fa fa-spin fa-spinner" /> SET
        </button>
      )
    }
    return (
      <button
        className={classNames("btn btn-primary", {
          "btn-warning ani-heartBeat ani-run infinite": showWarning,
        })}
        onClick={() => {
          MyModal.showFromMyUI(
            {
              type: "prj_set",
              data: {},
            },
            {
              title: "SET",
            }
          )
        }}
      >
        <i className="fa fa-sign-in" /> SET
      </button>
    )
  }
}

const SharedSET = {
  data: null,
  reloadBtn: null,
}
export { SharedSET }
export default MUIPJAPI
