/* eslint-disable */
import { Component } from "react"
import styled from "styled-components"
import { HUtils, MyPage } from "@macashipo/mlib"
import classNames from "classnames"
class CPagination extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {}
  onChangePage = page => {
    const { onChangePage } = this.props
    if (onChangePage) {
      onChangePage(page)
    }
  }
  render() {
    const { page, total, pageSize } = this.props
    if (total == 0 || pageSize <= 0) {
      return <div></div>
    }
    var countPage = parseInt(total / pageSize)
    if (total % pageSize > 0) {
      countPage += 1
    }
    if (countPage < 2) {
      return <div></div>
    }
    return (
      <WrapLayout>
        <nav>
          <ul className="pagination justify-content-center">
            <li className={classNames("page-item", { disabled: page < 2 })}>
              <a
                className="page-link"
                href="#"
                onClick={ev => {
                  ev.preventDefault()
                  this.onChangePage(page - 1)
                }}
              >
                <i className="fa fa-chevron-left" />
              </a>
            </li>
            {new Array(countPage).fill(1).map((v, i) => {
              return (
                <li
                  className={classNames("page-item", {
                    active: page == i + 1,
                  })}
                  key={i}
                >
                  <a
                    className="page-link"
                    href="#"
                    onClick={ev => {
                      ev.preventDefault()
                      this.onChangePage(i + 1)
                    }}
                  >
                    {i + 1}
                  </a>
                </li>
              )
            })}
            <li
              className={classNames("page-item", {
                disabled: page >= countPage,
              })}
            >
              <a
                className="page-link"
                href="#"
                onClick={ev => {
                  ev.preventDefault()
                  this.onChangePage(page + 1)
                }}
              >
                <i className="fa fa-chevron-right" />
              </a>
            </li>
          </ul>
        </nav>
      </WrapLayout>
    )
  }
}
const WrapLayout = styled.div``
export default CPagination
