import { Component } from "react"
import styled from "styled-components"
import { ApiGeneric } from "src/services/apiServices"
import Tabs from "react-responsive-tabs"
import { HUtils, MyPage } from "@macashipo/mlib"
import { MyModal, MyUI, Popover } from "src/core/components"
import classNames from "classnames"
const TypeFilter = {
  all: "all",
  todo: "todo",
  doing: "doing",
  done: "done",
  notdone: "notdone",
}
class MPPRJTasksFilters extends Component {
  constructor(props) {
    super(props)
    this.state = {
      filters: {},
    }
  }
  componentDidMount() {}
  requestTasks = () => {
    const fnList = this.props.fnList || {}
    HUtils.runFuntion(fnList, "reloadTasks", [{ filters: this.state.filters }])
  }
  _getTypeFilter = () => {
    let _filters = this.state.filters || {}
    if (Object.keys(_filters).length == 0) {
      return TypeFilter.all
    } else if (_filters.TaskStatusCode) {
      if (_filters.TaskStatusCode == "ToDo") {
        return TypeFilter.todo
      } else if (_filters.TaskStatusCode == "Done") {
        return TypeFilter.done
      } else if (_filters.TaskStatusCode == "Doing") {
        return TypeFilter.doing
      }
    } else if (_filters.TaskStatusNotDone) {
      return TypeFilter.notdone
    }
    return ""
  }
  _clickFilter = type => {
    let _filters = this.state.filters || {}
    if (type == TypeFilter.all) {
      _filters = {}
    } else if (type == TypeFilter.todo) {
      _filters.TaskStatusCode = "ToDo"
      delete _filters.TaskStatusNotDone
    } else if (type == TypeFilter.done) {
      _filters.TaskStatusCode = "Done"
      delete _filters.TaskStatusNotDone
    } else if (type == TypeFilter.doing) {
      _filters.TaskStatusCode = "Doing"
      delete _filters.TaskStatusNotDone
    } else if (type == TypeFilter.notdone) {
      delete _filters.TaskStatusCode
      _filters.TaskStatusNotDone = true
    }
    this.setState(
      {
        filters: _filters,
      },
      () => {
        this.requestTasks()
      }
    )
  }
  render() {
    var status = [
      { value: TypeFilter.all, name: "All" },
      { value: TypeFilter.todo, name: "ToDo" },
      { value: TypeFilter.doing, name: "Doing" },
      { value: TypeFilter.done, name: "Done" },
      { value: TypeFilter.notdone, name: "Not Done" },
    ]
    if (this.props.mini === true) {
      status = [
        { value: TypeFilter.all, name: "All" },
        { value: TypeFilter.notdone, name: "Not Done" },
      ]
    }
    let _showFilterProject = false
    let _options = HUtils.runFuntion(this.props.fnList, "getOptions", [])
    if (_options && _options.ProjectList && _options.ProjectList.length > 0) {
      _showFilterProject = true
    }
    console.log("_showFilterProject project:", _options)
    return (
      <WrapLayout>
        {status.map((v, i) => {
          return (
            <div
              key={i}
              className={classNames({
                "a-active": this._getTypeFilter() == v.value,
              })}
              onClick={() => {
                this._clickFilter(v.value)
              }}
            >
              {v.name}
            </div>
          )
        })}
        {_showFilterProject === true && (
          <Popover
            overlay={propsOverlay => {
              return (
                <MyUI
                  type="select_box_search_v2"
                  sourceData={_options?.ProjectList}
                  valueSelected={this.state.filters?.ProjectId}
                  onChange={itemOption => {
                    console.warn("onChange:", itemOption, itemOption.Value)
                    let _filters = this.state.filters || {}
                    _filters.ProjectId = itemOption.Value
                    _filters.ProjectId_Text = itemOption.Text
                    this.setState(
                      {
                        filters: _filters,
                      },
                      () => {
                        propsOverlay.hide && propsOverlay.hide()
                        this.requestTasks()
                      }
                    )
                  }}
                />
              )
            }}
            overlayClassName="m-popover"
            placement="bottom"
            destroyTooltipOnHide={true}
          >
            <div
              className={classNames("a-pick-project", {
                "a-active": this.state.filters.ProjectId != null,
              })}
            >
              {this.state.filters && this.state.filters.ProjectId
                ? `${this.state.filters.ProjectId_Text}`
                : "Ch·ªçn Project"}
            </div>
          </Popover>
        )}
      </WrapLayout>
    )
  }
}
const WrapLayout = styled.div`
  display: flex;
  & > div {
    background: white;
    padding: 0 8px;
    font-size: 12px;
    border: 1px solid #9f9f9f;
    border-radius: 10px;
    height: 20px;
    margin-right: 2px;
    cursor: pointer;
    color: #9f9f9f;
    &.a-active {
      background: #2fa4e7;
      color: white;
      border: 1px solid #0673b1;
    }
  }
`
export default MPPRJTasksFilters
