import { Component } from "react"
import styled from "styled-components"
import { ApiGeneric } from "src/services/apiServices"
import { HCF, HUtils, MyUI } from "@macashipo/mlib"
import { MyModal } from "src/core/components"
import { Spinner } from "reactstrap"
import MPPRJTasksFilters from "../MyUI/prj_task_filters"
import CPagination from "src/components/Pagination"
import BtnSet from "../MyUI/prj_set_btn"
class MPDailyTask extends Component {
  constructor(props) {
    super(props)
    this.state = {
      filters: {},
      assignByMe: false,
      pagination: {
        Page: 1,
        PageSize: 20,
      },
      total: 0,
    }
  }
  componentDidMount() {
    window.document.title = `DailyTasks - ${HCF.getCF("title")}`
    this.requestOptions()
  }
  requestOptions = () => {
    this.setState(
      {
        loading: true,
      },
      () => {
        ApiGeneric({
          request: {
            method: "POST",
            path: "/api/v1/UserTask_Mine",
            name: "Options",
          },
          data: {},
          successCallBack: response => {
            if (response.Data) {
              this.setState(
                {
                  options: response.Data,
                  loading: false,
                },
                () => {
                  this.requestTasks()
                }
              )
            }
          },
          errorCallBack: err => {
            console.log(err)
            this.setState({
              loading: false,
            })
          },
        })
      }
    )
  }
  requestTasks = () => {
    this.setState(
      {
        loading: true,
      },
      () => {
        ApiGeneric({
          request: {
            method: "POST",
            path: `/api/v1/${this._getApiPath()}`,
            name: this.state.assignByMe === true ? "GetTasksAssign" : "List",
          },
          data: {
            ...this._getRequestData(),
            ...this.state.filters,
            ...this.state.pagination,
          },
          successCallBack: response => {
            // if (this.state.assignByMe) {
            //   if (response.Data) {
            //     this.setState({
            //       tasks: response.Data,
            //       total: response.Data.length,
            //       loading: false,
            //     })
            //   }
            // }
            // if (response.Data.Data) {
            this.setState({
              tasks: response.Data.Data || [],
              total: response.Data.Total,
              loading: false,
            })
            // }
          },
          errorCallBack: err => {
            console.log(err)
            this.setState({
              loading: false,
            })
          },
        })
      }
    )
  }
  _getApiPath = () => {
    let _apiPath = "UserTask_Mine"
    const configFromConfigPage = this._getConfigFromConfigPage()
    if (configFromConfigPage.apiPath) {
      return configFromConfigPage.apiPath
    }
    return _apiPath
  }
  _getRequestData = () => {
    const configPage = this.props.configPage || {}
    let _requestData = configPage.RequestData || {}
    return _requestData
  }
  _getConfigFromConfigPage = () => {
    const configPage = this.props.configPage || {}
    const config = configPage.Config || {}
    return config
  }
  render() {
    const { tasks } = this.state
    const configFromConfigPage = this._getConfigFromConfigPage()
    // console.warn("render", this.props, configFromConfigPage.showAssignTo)
    return (
      <WrapLayout>
        <div
          className="a-header"
          style={
            configFromConfigPage.hideHeader === true ? { display: "none" } : {}
          }
        >
          <div className="a-left">
            <div className="a-title">My Tasks</div>
          </div>
          <div className="a-right">
            {/* <button
              className="btn btn-primary"
              onClick={() => {
                window.location.href = "/pj"
              }}
            >
              Projects
            </button> */}
            <button
              className="btn btn-primary"
              onClick={() => {
                MyModal.showFromMyUI(
                  {
                    type: "prj_dailytask",
                    fnList: {
                      reloadTasks: () => {
                        this.requestTasks()
                      },
                    },
                  },
                  {
                    title: "Report Task",
                    size: "xl",
                    backdrop: "static",
                  }
                )
              }}
            >
              <i className="fa fa-file-text-o" /> Report Task
            </button>
            <button
              className="btn btn-primary"
              onClick={() => {
                MyModal.showFromMyUI(
                  {
                    type: "prj_new_task",
                    data: {},
                    fnList: {
                      getCanEdit: () => {
                        return true
                      },
                      getOptions: () => {
                        return this.state.options
                      },
                      reloadTasks: () => {
                        this.requestTasks()
                      },
                    },
                  },
                  {
                    title: "New Task",
                  }
                )
              }}
            >
              <i className="fa fa-plus" /> New Task
            </button>
            <BtnSet />
            <button
              className="btn btn-primary"
              onClick={() => {
                MyModal.showFromMyUI(
                  {
                    type: "prj_task_user_setting",
                    data: {},
                  },
                  {
                    title: "User Setting",
                  }
                )
              }}
            >
              <i className="fa fa-cog" />
            </button>
          </div>
        </div>
        <div className="a-body">
          <div className="a-filter">
            <MPPRJTasksFilters
              fnList={{
                reloadTasks: ({ filters } = {}) => {
                  //eslint-disable-next-line
                  this.state.filters = filters || {}
                  //eslint-disable-next-line
                  this.state.pagination.Page = 1
                  this.requestTasks()
                },
                getOptions: () => {
                  return this.state.options
                },
              }}
            />
            <div style={{ marginLeft: "1rem" }}>
              <div class="form-check form-switch">
                <input
                  class="form-check-input"
                  type="checkbox"
                  id="cbAssignByMe"
                  defaultChecked={this.state.assignByMe}
                  onChange={ev => {
                    this.setState(
                      {
                        assignByMe: ev.currentTarget.checked,
                        pagination: {
                          Page: 1,
                          PageSize: 20,
                        },
                      },
                      () => {
                        this.requestTasks()
                      }
                    )
                  }}
                />
                <label class="form-check-label" for="cbAssignByMe">
                  Assigned tasks to others (by me)
                </label>
              </div>
            </div>
          </div>
          {tasks != null && (
            <div className="a-tasks">
              {tasks.length > 0 ? (
                <MyUI
                  type="grid"
                  data={tasks}
                  typeItem="prj_task_item"
                  morePropsItem={{
                    mode: "list",
                    showAssignTo: configFromConfigPage.showAssignTo,
                  }}
                  fnList={{
                    onClickItem: v => {
                      console.log("onClick item:", v)
                      MyModal.showFromMyUI(
                        {
                          type: "prj_task_item",
                          data: v,
                          mode: "detail",
                          fnList: {
                            getCanEdit: () => {
                              return true
                            },
                            getOptions: () => {
                              return this.state.options
                            },
                            afterUpdateData: newData => {
                              for (let k of Object.keys(newData)) {
                                v[k] = newData[k]
                              }
                              this.forceUpdate()
                            },
                            reloadTasks: () => {
                              this.requestTasks()
                            },
                          },
                        },
                        {
                          title: "Task",
                          backdrop: "static",
                        }
                      )
                    },
                  }}
                />
              ) : (
                <div className="a-tasks-empty">
                  <img
                    src="https://cdn.shopify.com/s/files/1/0262/4071/2726/files/emptystate-files.png"
                    width={"120px"}
                    alt="empty"
                  />
                  <div>You don't have any tasks to show</div>
                  <div>Create new one</div>
                </div>
              )}
            </div>
          )}
          <div className="a-pagination">
            <CPagination
              page={this.state.pagination.Page}
              total={this.state.total}
              pageSize={this.state.pagination.PageSize}
              onChangePage={p => {
                this.setState(
                  {
                    pagination: {
                      Page: p,
                      PageSize: this.state.pagination.PageSize,
                    },
                  },
                  () => {
                    this.requestTasks()
                  }
                )
              }}
            />
          </div>
          {tasks == null && this.state.loading === true && (
            <div className="a-tasks-empty">
              <Spinner />
              <div>Loading...</div>
            </div>
          )}
        </div>
      </WrapLayout>
    )
  }
}

const WrapLayout = styled.div`
  & > .a-header {
    display: flex;
    // border-bottom: 1px solid #1c4e7e;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.01), 0 4px 4px rgba(0, 0, 0, 0.02),
      0 10px 8px rgba(0, 0, 0, 0.03), 0 15px 15px rgba(0, 0, 0, 0.03);
    & > .a-left {
      & > .a-title {
        font-size: 20px;
      }
    }
    & > .a-right {
      margin-left: auto;
      & > * {
        margin-left: 2px;
      }
    }
  }
  & > .a-body {
    padding: 10px;
    background: white;
    border-radius: 4px;
    & .a-filter {
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      & .form-switch {
        padding-left: 2.5em;
        margin: 0px;
      }
    }
    & .a-tasks-empty {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      // background: white;
      // border-radius: 10px;
      padding: 2rem;
    }
    & .a-pagination {
      margin-top: 1rem;
      & .page-item.active .page-link {
        background-color: #0082ff;
      }
      & .page-item .page-link {
        border-radius: 0px;
      }
    }
  }
`

export default MPDailyTask
